import React from 'react'
import { Link } from 'gatsby'
import { Spring } from 'react-spring'
import { delay } from 'delay'

const Footer = () => (
  <Spring
  delay={1000}
  from={{ height: 0, opacity: 0, bottom: -50 }}
  to={{ height: 80, opacity: 1, bottom: 0}}
  >
  {({ height, opacity, bottom }) =>

  <footer
  id="footer"
  style={{ height, opacity, bottom}}
  >
</footer>
}

    </Spring>
)

export default Footer
